.root {
  position: absolute;
  border-radius: 50%;
  font-size: 1rem;
  height: 2.2rem;
  width: 2.2rem;
  border: 2px dashed #faf9f7;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.3);
}

.list {
  position: absolute;
  background-color: rgba(250, 249, 247, 0.8);
  color: #27241d;
  width: 5rem;
  border-radius: 0.4rem;
  list-style: none;
  padding: 0;
  cursor: pointer;
  transform: translate(32%, 15%);
}

.list-item {
  padding: 8px 12px;
  transition: transform 250ms;
  cursor: pointer;
}

.list-item:hover {
  transform: scale(1.1);
}

@media (min-width: 768px) {
  .root {
    font-size: 1.1rem;
    height: 6.42rem;
    width: 6.42rem;
    border: 5px dashed #faf9f7;
  }
  .list {
    height: auto;
    width: 6.6rem;
    transform: translate(72%, 48%);
  }
}

@media (max-width: 768px) {
  .root {
    transform: translate(-120%, -120%);
  }
}
